html,body,div{
  margin:0;
  padding:0;
}
*{
  box-sizing:border-box;
}
html,body,main,.app,.main{
  width:100%;
  height:100%;
}
body{
  font-family:'SegoeUI',sans-serif;
  font-size:16px;
  line-height:1.5;
}
input,button{
  font-family:inherit;
  font-size:inherit;
  line-height:inherit;}
p{
  margin:006px0;
}
input{
  padding:4px6px;
}
button{
  padding:6px12px;
  background-color:#00A5C6;
  color:white;
  font-weight:600;
  border:none;
  border-radius:2px;
}
button:hover{
  background-color:#00BBE0;
}
.row{
  display:flex;
}
.row>*{
  min-width:0;
  flex:1;
}
.column{
  display:flex;
  flex-direction:column;
}
.column>*{
  min-height:0;
  flex:1;
}
.login{
  max-width:400px;
  margin:0auto;
  text-align:center;
}
.left-pane{
  flex:00300px;
}
.my-tag{
  padding:6px10px;
  font-weight:500;
}
.text-input-and-button{
  display:flex;
  align-items:center;
}
.text-input-and-button>*:first-child{
  flex:1;
}
.left-pane.text-input-and-button{
  margin:6px10px;
}
.left-pane.text-input-and-buttonbutton{
  flex:00auto;margin:0;
}
.conversation-card{
  cursor:pointer;
  padding:6px10px;
  overflow:hidden;
  text-overflow:ellipsis;
}
.conversation-card:hover{
  background-color:#eee;
}
.conversation-card.channel-name{
  font-weight:500;
  color:black;
}
.conversation-card.time{
  text-align:right;
  color:#888;
}
.conversation-card.last-message{
  color:#888;
  text-overflow:ellipsis;
  white-space:nowrap;
}
@media(max-width:650px){
  .left .right-pane{
    display:none;
  }
  .right .left-pane{
    display:none;
  }
  .left .left-pane{
    flex:1;
  }
  .right-pane {
    border: none !important; 
  }
}
.right-pane{
  border:solid#aaa;
  border-width:0001px;
}
.right-pane.conversation-header{
  padding:6px8px;
  font-weight:500;
  color:black;
  flex:00auto;
  align-items:center;
}
.right-pane.conversation-header>p{
  margin:6px10px;
}
.right-pane.conversation-header>button{
  flex:00auto;
}
@media(min-width:650px){
  .only-narrow{
    display:none;
  }
}
.messages{
  border:solid#aaa;
  border-width:1px0;
  overflow-y:scroll;
}
.message-card{
  margin:6px8px;
}
.message-card.own{
  margin:6px8px;
  text-align:right;
}
.message-card.bubble{
  display:inline-block;
  background:white;
  padding:6px10px;
  border-radius:5px;
  box-shadow:1px1px5pxgray;
}
.message-card.own.bubble{
  background:#eee;
}
.message-card.time{
  margin:00010px;
  color:#aaa;
}
.right-pane.send-message{
  padding:6px8px;
  flex:00auto;
}